export default {
	data() {
		return {
			bbs: []
		}
	},
	onShow: function() {
		uni.onSocketMessage((res) => {
			this.getMessage(JSON.parse(res.data))
		})
		getApp().sendSocket("selectBBS")
	},
	methods: {
		getMessage: function(data) {
			uni.hideLoading()
			let msg = data.msg
			let obj = data.obj
			if (msg == "loginError") {
				getApp().delCode()
				uni.redirectTo({
					url: "/pages/mine/mine"
				})
			} else if (msg == "bbs") {
				this.bbs = obj
			}
		},
		barTo: function(e) {
			let name = e.currentTarget.dataset.name
			if (name == "scan") {
				getApp().scan()
			} else {
				getApp().barTo(name)
			}
		},
		intoBBSinfo: function (e) {
			let id = e.currentTarget.dataset.bbsid
			uni.navigateTo({
				url: '/pages/home/bbsInfo?id=' + id
			})
		}
	}
}
